import React, { Component } from 'react';

import './RepList.css';

class RepList extends Component {
    render() {
        const { reps } = this.props;
        return (
            <div>
                <h2>
                    List / Rep
                </h2>
                <div className="list-header">
                    <h4>Name</h4>
                    <h4>Party</h4>
                    {reps && reps[0] && reps.map((rep) =>
                        <div>
                            <h4>{rep.name}</h4>
                            <h4>{rep.party}</h4>
                        </div>
                    )}
                </div>
            </div>
        )
    }
}

export default RepList;


