import React, { Component } from 'react';

import './DropDown.css';

class DropDown extends Component {
    constructor(props) {
        super(props);
        this.handleSelect = this.handleSelect.bind(this);
    }
    handleSelect(e) {
        console.log(this.props);
        const selection = e.target.value;
        if (selection.length === 1) this.props.handleChange(selection);
        else this.props.handleChange(selection, true)
    }
    render() {
        const { options } = this.props;
        return (
            <div>
                <select
                    onChange={this.handleSelect}
                >
                    <option></option>
                    {options.map((option) =>
                        <option
                            value={option.id}
                            key={option.id}
                        >
                            {option.name}
                        </option>
                    )}
                </select>
            </div>
        )
    }
}

export default DropDown;


