import React, { Component } from 'react';

import './RepDetails.css';

class RepDetails extends Component {
    render() {
        const { 
            name,
            district,
            phone,
            office
         } = this.props.rep;
        return (
            <div>
                <div>{name ? name.split(' ')[0] : 'First Name'}</div>
                <div>{name ? name.split(' ')[1] : 'Last Name'}</div>
                <div>{district ? district ? district : 'N/A' : 'District'}</div>
                <div>{phone ? phone : 'Phone Number'}</div>
                <div>{office ? office : 'Office'}</div>
            </div>
        )
    }
}

export default RepDetails;


